label {
    display: inline-block;
    margin: 10px 0;
  }
  
  .input-add {
  border: 1px solid #dedede;
  border-radius: 2px;
  display: block;
  font-size: 16px;
  padding: 10px;
  width: 100%;
}
  
.btn {
  cursor: pointer;
  background-color: var(--purple);
  box-shadow: var(--box-shadow);
  color: #fff;
  border: 0;
  display: block;
  font-size: 16px;
  margin: 10px 0 30px;
  padding: 10px;
  width: 100%;
  transition-property: box-shadow; 
  transition-duration: 0.3s; 
}
  
.btn:hover {
  box-shadow: var(--box-shadow-purple);
}
  
  