.button-container {
    display: flex; 
    flex-direction: row; 
    justify-content: flex-end;
    margin-bottom: -10px; 
}

.button-container button {
    cursor: pointer; 
    border: none; 
    margin: 0 5px; 
    border-radius: 5px; 
    box-shadow: var(--box-shadow);
}

.income-button {
    border: 1px solid #2ecc71 !important; 
    color: #2ecc71; 
}

.expense-button {
    border: 1px solid #c0392b !important; 
    color: #c0392b; 
}

.income-button.show {
    background-color: #2ecc71; 
    color: #333; 
}

.expense-button.show {
    background-color: #c0392b; 
    color: #333; 
}
