.popup {
    padding: 2.5px 10px 5px 10px; 
    position: absolute; 
    top: -50%;
    right: -20%; 
    height: auto;
    background-color: #fff;
    box-shadow: var(--box-shadow);
    border: 1px solid #333;
    z-index: 1; 
    width: 70%; 
}

.popup.hide {
    display: none; 
}

.header {
    display: flex; 
    justify-content: space-between; 
    height: 24px; 
}

.header button {
    align-self: flex-start;
    cursor: pointer;
    background-color: var(--purple);
    box-shadow: var(--box-shadow);
    color: #fff;
    border: 0;
    font-size: 14px;
    padding: 0; 
    width: 16px;
    height: 16px;  
    transition-property: box-shadow;
    transition-duration: 0.3s; 
}

.header button:hover {

    box-shadow: var(--box-shadow-purple); 
}

.popup h5 {
    align-self: flex-end; 
    margin: 0;  
    font-size: 15px;
}

.label-popup {
    display: inline-block;
    margin-top: 10px;
    font-size: 12px; 
}

.input-popup {
    border: 1px solid #dedede;
    border-radius: 2px;
    display: block;
    font-size: 12px;
    padding: 5px;
    width: 100%;
}

.btn-popup {
    cursor: pointer;
    background-color: var(--purple);
    box-shadow: var(--box-shadow);
    color: #fff;
    border: 0;
    display: block;
    font-size: 14px;
    letter-spacing: 1px;
    margin: 10px 0;
    padding: 5px;
    width: 100%;
    transition-property: box-shadow;
    transition-duration: 0.3s; 
}

.btn-popup:hover {
    box-shadow: var(--box-shadow-purple); 
}

