.inc-exp-container {
    position: relative;
    background-color: #fff;
    box-shadow: var(--box-shadow);
    padding: 20px;
    display: flex;
    justify-content: space-between;
    margin: 20px 0;
}

.income-side, .expenses-side {
    cursor: pointer; 
}
.income-side:hover .income-expenses-window.left {
    opacity: 0.75; 
}

.expenses-side:hover .income-expenses-window.right {
    opacity: 0.75; 
}

.inc-exp-container > div {
    flex: 1;
    text-align: center;
}

.inc-exp-container > div:first-of-type {
    border-right: 1px solid #dedede;
}


.money {
    font-size: 20px;
    letter-spacing: 1px;
    margin: 5px 0;
}

.money.plus {
color: #2ecc71;
}

.money.minus {
color: #c0392b;
}

