.list {
    list-style-type: none;
    padding: 0;
    margin-bottom: 40px;
}

.list li {
    cursor: pointer;
    background-color: #fff;
    box-shadow: var(--box-shadow);
    color: #333;
    display: flex;
    justify-content: space-between;
    position: relative;
    padding: 10px;
    margin: 10px 0;
}

.list li.plus {
    border-right: 5px solid #2ecc71;
}

.list li.minus {
    border-right: 5px solid #c0392b;
}


.buttons {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(-100%, -50%);
    transition: opacity 0.3s ease;
    display: flex; 
    flex-direction: column; 
    justify-content: center;
    gap: 2px; 
    width: 7%; 
    height: 120%; 
    opacity: 0;
}

.delete-btn, .update-btn {
    cursor: pointer;
    padding: 2px; 
    border: 0;
    color: #fff;
    font-size: 12px; 
}

.delete-btn {
    background-color: #e74c3c;
}

.update-btn {
    background-color: #3498db;
}

.list li:hover .buttons {
    opacity: 1;
}
