.income-expenses-window {
    position: absolute; 
    box-shadow: var(--box-shadow); 
    background: #fff;
    width: 150px; 
    height: auto;
    top: 50%;   
    opacity: 0; 
    transition-property: opacity;
    transition-duration: 0.5s; 
    font-size: 12px; 
}

.income-expenses-window.left {
    left: -30%; 
}

.income-expenses-window.right {
    right: -30%; 
}

.income-expenses-ul {
    list-style-type: none;
    text-align: start;
    margin: 0; 
    padding: 5px;
}

.income-expenses-li {
    display: flex; 
    flex-direction: row;
    justify-content: space-between;
}

.income-expenses-text.income {
    color: #2ecc71; 
}

.income-expenses-text.expenses {
    color: #c0392b; 
}

.income-expenses-amount.income {
    font-weight: bold; 
    color: #367E18; 
}

.income-expenses-amount.expenses {
    font-weight: bold; 
    color: #CC3636; 
}